import React from 'react';
import PropTypes from 'prop-types';
import withStyles from 'isomorphic-style-loader/lib/withStyles';
import Breadcrumb from '../../components/Breadcrumb';
import SidebarAboutUs from '../../components/SidebarAboutUs';
import s from './MeetOurTeam.css';

class MeetOurTeam extends React.Component {
  static propTypes = {
    title: PropTypes.string.isRequired,
  };

  render() {
    return (
      <div className={s.root}>
        <Breadcrumb title={this.props.title} />
        <div className={s.container}>
          <SidebarAboutUs title={this.props.title} />
          <div className={s.contents}>
            <h1>
              {this.props.title}
            </h1>
            <p>
              Each and every one of our employees represent the spirit of our
              company: driven, committed and honesty. We are continuously
              ensuring not just an effective workforce, but a well-rounded,
              talented and complementary team who are always ready to serve our
              customers and community.
            </p>
          </div>
        </div>
      </div>
    );
  }
}

export default withStyles(s)(MeetOurTeam);



